import Link from 'next/link'
import Navbar from 'react-bootstrap/Navbar'
import Nav from 'react-bootstrap/Nav'

const mainStyle = {
  background: 'linear-gradient(45deg, #FE6B8B 30%, #FF8E53 90%)',
  position: 'sticky',
  top:'0',
  width: '100%',
  transition:'all 0.5s',
  zIndex: '1'
}

const wordCapitalize = {
  textTransform: "capitalize"
}

const Header = () => {
  return (
    <Navbar collapseOnSelect expand="lg" style={mainStyle}>
      <Navbar.Brand href="/">NextJS with Bootstrap</Navbar.Brand>
      <Navbar.Toggle aria-controls="responsive-navbar-nav" />
      <Navbar.Collapse id="responsive-navbar-nav">
        <Nav className="mr-auto">
          <Link href="/">
            <a className="nav-item nav-link ">Home</a>
          </Link>
          {
            getHeaders().map(header => (
              <HeaderLink key={header.id} header={header} />
            ))
          }
        </Nav>
      </Navbar.Collapse>
    </Navbar>
  )
}

// 使用function作爲api承接內容，在上方的return中map接到的值
const getHeaders = () => {
  return [
    { id: 'about', title: 'About' },
    { id: 'batman', title: 'Batman' },
    { id: 'contact', title: 'Contact' },
    { id: 'login', title: 'Login' },
  ]
}

const HeaderLink = ({ header }) => (
  <Link href="/[headers]" as={`/${header.id}`}>
    <a className="nav-item nav-link " style={wordCapitalize}>{header.title}</a>
  </Link>
)

// // 使用陣列把連結塞入map中
// const headers = ['about', 'batman', 'contact', 'login']

export default Header
